"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import axiosInstance from "../../../ultis/axios";

type Project = {
  _id: string;
  title: string;
  code: string;
};

export default function DashboardPage() {
  const router = useRouter();
  const [projects, setProjects] = useState<Project[] | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const token = typeof window !== 'undefined' ? (sessionStorage.getItem('token') || localStorage.getItem('token')) : null;
    if (!token) {
      router.replace('/login');
      return;
    }
    (async () => {
      try {
        // Assuming endpoint exists: /api/projects (adjust if different)
        const res = await axiosInstance.get('/api/projects');
        const list = Array.isArray(res.data) ? res.data : (res.data?.items || []);
        if (!list || list.length === 0) {
          router.replace('/no-project');
          return;
        }
        setProjects(list);
      } catch (e: any) {
        if (e?.response?.status === 404) {
          router.replace('/no-project');
          return;
        }
        setError(e?.response?.data?.message || 'Không thể tải danh sách dự án');
      } finally {
        setLoading(false);
      }
    })();
  }, [router]);

  if (loading) return <main className="p-6">Đang tải...</main>;
  if (error) return <main className="p-6 text-red-600">{error}</main>;
  if (!projects) return null;

  return (
    <main className="p-6">
      <div className="mx-auto w-full max-w-5xl">
        <div className="mb-6 flex items-center justify-between">
          <h1 className="text-2xl font-semibold" style={{color:'var(--primary)'}}>Dự án của bạn</h1>
          <button className="btn-primary rounded-lg px-4 py-2">+ Tạo dự án</button>
        </div>
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
          {projects.map((p) => (
            <div key={p._id} className="card rounded-xl p-4">
              <div className="text-lg font-medium">{p.title}</div>
              <div className="text-sm opacity-70">Mã: {p.code}</div>
              <div className="mt-3">
                <button className="px-3 py-2 rounded-lg" style={{border:'1px solid var(--border)'}}>Mở</button>
              </div>
            </div>
          ))}
        </div>
      </div>
    </main>
  );
}


